<template>
    <div ref="trys" style="height: calc(100% - 0em)">
        <div v-if="showInfo"  class="info-container">
            <info :row="row" @closeDiv="closeDiv" :controls="controls" :deviceType="searchParam.deviceType"></info>
        </div>
        <div v-show="!showInfo" class="all-container">
          <div class="operate-div" ref="operateDiv">
            <template v-for="(item, $index) in FormOpt">
              <inputs ref="inputs" v-if="item.type == 'input'" :option="item.options"
                      @getInput="getFrom"></inputs>
              <selects ref="selects"  v-if="item.type == 'select'" :option="item.options" @getSelect="getFrom"></selects>
              <datePicker ref="timer" type="doubleDate" style="margin-left:10px" v-if="item.type == 'timer'" :dateOpt="item.options" @getDatePicker="getFrom"></datePicker>
            </template>
          </div>
          <!-- <div class="deepFilter" ref="deepFilter" v-if="deepSearchData.length > 0">
            <label>筛选条件：</label>
            <template v-for="(data, $index) in deepSearchData">
            <span class="deepSearchSpan" :style="{'padding-right': data.flag ?  '1.5em' : '0.5em'}" >{{data.label}}： {{data.value}}
              <i v-if="data.flag" @click=" removeSearchData($index)">
                <icon name="remove" scale="0.8"></icon>
              </i>
            </span>
            </template>
          </div> -->
          <div ref="myOwnTable" class="table-container" :style="{height: tableHeight || 'calc(100% - 5em)'}">
            <tables ref="myTable" :tableOpt="TableOpt" class="tablePadding"
                    @rowDbClick="clickTableRow"></tables>
          </div>
        </div>
      <dialogs type="alert" ref="alerts"></dialogs>
    </div>
</template>
<script>
    import info from './Info.vue'
    export default {
        data() {
            return {
                searchParam: {
                  deviceType: '',
                  changeDateStart: '',
                  changeDateEnd: '',
                  meterUserNo: '',
                  deviceNo: '',
                  customerName: '',
                  customerContact: '',
                  customerAddr: ''
                },
                showTable: false,
                tableHeight: '',
                row: '', //选中的行数据
                showInfo: false, //是否显示详情
                controls: [],
                deepSearchData: '',
                searchType: 'meterUserNo',
                measureDate: '',
                TableOpt: {
                    column: [
                        {title: '用户姓名', field: 'customerName', textAlign: 'center', width: '120'},
                        {title: '表计户号', field: 'meterUserNo', textAlign: 'center', width: '120'},
                        {title: '联系电话', field: 'customerContact', textAlign: 'center', width: '120'},
                        {title: '旧表号', field: 'meterNo', textAlign: 'center', width: '150'},
                     //   {title: '旧表剩余金额', field: 'remainAmount', textAlign: 'center', width: '120'},
                        {title: '旧表总示示数', field: 'power', textAlign: 'center', width: '120'},
                        {title: '新表号', field: 'newMeterNo', textAlign: 'center', width: '150'},
                     //   {title: '新表剩余金额', field: 'newRemainAmount', textAlign: 'center', width: '120'},
                        {title: '新表总示示数', field: 'newPower', textAlign: 'center', width: '120'},
                        {title: '用户地址', field: 'customerAddr', textAlign: 'center', width: '220'},
                        {title: '换表人员', field: 'userName', textAlign: 'center', width: '100'},
                        {title: '换表时间', field: 'changeTime', textAlign: 'center', width: '160'}
                    ],
                    url: 'changeMeter/queryChangeMeters',
                    method: 'post',
                    isExport: true,
                    exportURL: 'changeMeter/queryChangeMetersDownload',
//                    url: 'dist/json/changeMeter.json',
//                    method: 'get',
                    pageSize: 20,
                    pageNumber: 1,
                    pageList: [20, 40, 60],
                    sortName: '',
                    sortOrder: 'asc',
                    height: document.body.clientHeight - 380,
                    queryParam: {}
                }
            }
        },
        components: {
            info
        },
        computed: {
          FormOpt: function() {
            let dates = this.initDate(true);
              return [
                {
                  type: 'select',
                  options: {
                    width: '8em',
                    cs: 'client',
                    data: this.getEnumItem('deviceType').obj,
                    key: 'searchParam.deviceType'
                  }
                },
                {type: 'timer',
                  options: {
                    key: 'measureDate',
                    value: {startDate: dates.start, endDate: dates.end},
                    width: '23em',
                    fontSize: '0.8em'
                  }
                },
                {
                  type: 'input',
                  options: {
                    type: 'typeSearch',
                    key: 'searchParam',
                    selectOpt: {
                      width: '7em',
                      cs: 'client',
                      data: [
                        {text: '表计户号', value: 'meterUserNo'},
                        {text: '用户表号', value: 'deviceNo'},
                        {text: '用户姓名', value: 'customerName'},
                        {text: '联系电话', value: 'customerContact'},
                        {text: '用户地址', value: 'customerAddr'}],
                      text: 'text',
                      value: 'value',
                      key: 'searchType'
                    }
                  }
                }
              ]
          },
            alerts: function () {
                return this.$refs.alerts
            }
        },
        methods: {
            getFrom: function (val) {
              if (val.combine) {
               if (this.searchParam[val.key.split('.')[1]] !== val.val) {
                 this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'changeDateStart', 'changeDateEnd']);
                 this.getOption(val, true);
                 this.getParams(1);
               }
             } else {
               this.getOption(val, true);
             }
           },
            closeDiv: function () {
                this.showInfo = false;
                this.getParams(1);
            },
            //单击表格事件
            clickTableRow: function (val) {
                this.row = val.val;
                this.showInfo = true;
            },
            removeSearchData: function (index) {
                let key = this.deepSearchData[index].key;
                this.searchParam[key] = this.$refs['inputs'][0].search = '';
                this.getParams(1)
            },
            getParams: function (pageNumber) {
              this.deepSearchData = [];
              let arr = this.getSearchData(this.searchParam);
              let labelName = '';
              for(let key in arr) {
                let value = arr[key];
                let flag = true;
                switch (key) {
                  case 'meterUserNo':
                    labelName = '表计户号';
                    break;
                  case 'deviceNo':
                    labelName = '用户表号';
                    break;
                  case 'customerName':
                    labelName = '用户姓名';
                    break;
                  case 'customerContact':
                    labelName = '联系电话';
                    break;
                  case 'customerAddr':
                    labelName = '用户地址';
                    break;
                  case 'changeDateStart':
                    labelName = '开始时间';
                    flag = false;
                    break;
                  case 'changeDateEnd':
                    labelName = '结束时间';
                    flag = false;
                    break;
                  case 'deviceType':
                    labelName = '设备类型';
                    flag = false;
                    value = this.getEnumItem('deviceType', value).text;
                    break;
                }
                this.deepSearchData.push({label: labelName, value: value, key: key, flag: flag})
              }
              this.getHeight();
              this.searchTable(arr, pageNumber);
            },
            resetDeepData: function(val) {
              let that = this;
              let data = that.initDate(true);
              that.searchParam.changeDateStart = data.start;
              that.searchParam.changeDateEnd = data.end;
              that.deepSearchData = [];
              that.deepSearchData.push({label: '设备类型', value: this.getEnumItem('deviceType', val).text, key: 'deviceType', sign: false});
              that.deepSearchData.push({label: '开始时间', value: data.start, key: 'changeDateStart', sign: false});
              that.deepSearchData.push({label: '结束时间', value: data.end, key: 'changeDateEnd', sign: false});
          },
          windowChange: function () {
            let that = this;
            that.getHeight();
           /* window.onresize = function () {
              that.getHeight();
            }*/
          },
          init: function () {
                let that = this;
                let data = that.initDate(true);
                that.TableOpt.isExport = that.controls[6];
                that.TableOpt.queryParam['deviceType'] = '07';
                that.TableOpt.queryParam['changeDateStart'] = data.start;
                that.TableOpt.queryParam['changeDateEnd'] = data.end;
                that.showTable = true;
              /*window.onresize = function () {
                that.windowChange();
              }*/
          }
        },
        mounted: function () {
          this.isIn();
        
          this.getAction();
        },
        watch: {
            'searchParam.deviceType': {
                handler(val, oldVal){
                  if (val !== oldVal) {
                    let dates = this.initDate(true);
                    this.$refs['inputs'][0].search = '';
                    this.searchParam = this.cleanSearchData(this.searchParam, ['deviceType', 'changeDateStart', 'changeDateEnd']);
                    if (dates.start !== this.searchParam['changeDateStart'] || dates.end !== this.searchParam['changeDateEnd'] ) {
                      this.$refs.timer[0].dateOpt.value = {startDate: dates.start, endDate: dates.end};
                      this.$refs.timer[0].init();
                      this.resetDeepData(val);
                      this.measureDate = dates.start + '至' + dates.end;
                    } else {
                      this.getParams(1)
                    }
                  }
                }
            },

            measureDate: {
                handler(val, oldVal) {
                  if (val !== '' && val !== oldVal) {
                    let arr = val.split('至');
                    this.searchParam.changeDateStart = arr[0];
                    this.searchParam.changeDateEnd = arr[1];
                    this.getParams(1)
                  }
                }
            }
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";

</style>



