<template>
    <div class="info-div" style="height: 100%;">
      <div class="info-header">
        <span class="callback" @click="resetComponent">换表记录 </span>
        <span> > 记录详情</span>
      </div>
        <VuePerfectScrollbar class="info-container" v-if="show" >
            <div class="list-content detail-content list-contentB">
              <div class="list-header">
                <i class="hasIcon hasImg">
                  <img src="dist/img/file/user-message.png" />
                </i>
                客户资料
              </div>
              <ul  class="form-list detail-list">
                <li v-for="(items, $indexs) in list.customerInfo" >
                  <span class="label-span" style="min-width:7.5em">
                  {{items.name}} : &nbsp;
                  </span>
                  <span :title="items.value" class="label-span" style="max-width: calc(100% - 8em)">{{getValue(items.value)}}</span>
                </li>
              </ul>
            </div>
            <div class="list-content detail-content list-contentB" >
              <div class="list-header">
                <i class="hasIcon hasImg">
                  <img src="dist/img/file/change-infor-icon.png" />
                </i>更换信息
              </div>
              <table class="form-list table-list"  style="width: 60%;min-width: 20em;margin: 1em 0;font-size:0.875em">
                <tr class="table-header">
                  <th></th>
                  <th>旧表信息</th>
                  <th>新表信息</th>
                </tr>
                 <tr >
                  <td>表号</td>
                  <td>{{deviceList.meterNo}}</td>
                  <td>{{deviceList.newMeterNo}}</td>
                </tr>
                <tr >
                  <td>总{{typeName}}能示数</td>
                  <td>{{deviceList.power}}</td>
                  <td>{{deviceList.newPower}}</td>
                </tr>
             <!--    <tr >
                  <td>剩余金额</td>
                  <td>{{deviceList.remainAmount}}</td>
                  <td>{{deviceList.newRemainAmount}}</td>
                </tr> -->
                <tr >
                  <td>表计倍率</td>
                  <td>{{deviceList.ratio}}</td>
                  <td>{{deviceList.newRatio}}</td>
                </tr>
                <tr >
                  <td>表计规约</td>
                  <td>{{getEnumItem(deviceType === '07' ?　'commRule' : 'commRules',deviceType === '07' ? deviceList.commRule + '-' + deviceList.meterType : deviceList.commRule).text}}</td>
                  <td>{{getEnumItem(deviceType === '07' ?　'commRule' : 'commRules',deviceType === '07' ? deviceList.newCommRule + '-' + deviceList.newMeterType : deviceList.newCommRule).text}}</td>
                </tr>

                <tr >
                  <td>购{{typeName}}次数</td>
                  <td>{{deviceList.payCount}}</td>
                  <td>{{deviceList.newPayCount}}</td>
                </tr>
                <tr >
                  <td>充值总额</td>
                  <td>{{deviceList.totalAmount}}</td>
                  <td>{{deviceList.newTotalAmount}}</td>
                </tr>
              </table>
              <ul  class="form-list" style="width:100%; float:left">
                <li v-for="(items, $indexs) in list.changeInfo" :style="{width: items.width, margin:'0.1em 1em'}" v-if=" list.changeInfo !== 'undefined'" >
                  <span class="label-span" style="min-width:7.5em">
                   {{items.name}} : &nbsp;
                  </span>
                  <span :title="items.value" class="label-span" style="max-width: calc(100% - 8em)">{{getValue(items.value)}}</span>
                </li>
              </ul>
            </div>
        </VuePerfectScrollbar>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                customerList:'',
                deviceList: '',
                show: true,
                selected: '',
                accountInfo: '',
                showPrice: false,
                customerNo: '',
                formOpt: [
                    {
                        name: '客户资料',
                      img: 'dist/img/file/user-message.png',
                        color: '#F59B7C',
                        listType: 'list',
                        list: {data: 'customerInfo', isShow: true}
                    },
                    {
                        name: '更换详情',
                        img: 'dist/img/file/change-infor-icon.png',
                        color: 'orange',
                        listType: 'table',
                        list: {data: 'changeInfo', isShow: true}
                    }
                ]
            }
        },
        components: {
        },
        props: ['row', 'controls', 'deviceType'],
        methods: {
            reAccount: function () {
                this.$parent.openCard(this.selected, 2)
            },
            resetComponent: function () {
                let that = this;
                that.show = false;
                that.$emit('closeDiv');
                setTimeout(function () {
                    that.show = false;
                }, 0)
            },
            cancelPayments: function () {
                 this.$parent.cancelPayment(this.selected)
            },
            getInfoData: function () {
                let param = {};
                param['customerNo'] = this.selected.customerNo;
             //   param['deviceType'] = this.deviceType;
                param['newCno'] = this.selected.newCno;
                let that = this;
                that.getRequest({
                    url: 'changeMeter/queryChangeMeterDetail',
                    method: 'post',
//                    method: 'get',
//                    url: 'dist/json/changeDetail.json',
                    param: param,
                    success: function (data) {
                      let Data = data.data;
                      that.customerList = Data.customerInfo;
                      that.deviceList = Data.changeMeterDetailInfo;
                    },
                  error: function () {
                    that.customerList = '';
                    that.deviceList = '';
                  }
                }, true)
            },
            init: function () {
                this.selected = this.row;
                this.getInfoData();
            }
        },
        computed: {
            alerts: function () {
                return this.$parent.alerts
            },
            loadings: function () {
                return this.$parent.loadings
            },
            typeName: function () {
                let type;
                switch (this.deviceType) {
                    case '07':
                        type = '电';
                        break;
                    case '08':
                        type = '水';
                        break;
                    case '09':
                        type = '气';
                        break;
                }
                return type;
            },
            list: function () {
                let key = 'electMeterNo';
                switch (this.deviceType) {
                  case '07':
                    key = 'electMeterNo';
                    break;
                  case '08':
                    key = 'waterMeterNo';
                    break;
                  case '09':
                    key = 'gasMeterNo';
                    break;
                 }
                  return{
                        customerInfo: [
                            {name: '用户姓名', value: this.customerList.customerName},
                            {name: '门牌编号', value: this.customerList.propertyName},
                            {name: '联系电话', value: this.customerList.customerContact},
                   //         {name: '用户表号', value: this.customerList[key]},
                            {name: '计费类型', value: this.customerList.dictItemName},
                   //         {name: '表计户号', value: this.customerList.meterUserNo},
                            {name: '用户地址', value: this.customerList.customerAddr}
                        ],
                        changeInfo: [
                            {name: '更换人员', value: this.deviceList.changeUserName,  width: '20%'},
                            {name: '更换时间', value: this.deviceList.changeTime, width: '25%'},
                            {name: '更换原因', value: this.deviceList.changeRemark, width: '100%'}
                        ]
                    };
            }
        },
        mounted: function () {
            this.isIn();
            this.init();
        },
        watch: {
            'deviceType': {
                handler(val, oldVal) {
                    if (val !== oldVal) {
                       this.getInfoData();
                    }
                }
            }
        }
    }
</script>
<style lang="scss">
    @import "../../../assets/main.scss";
    button.cardOperate {
        padding:5px 12px;
        margin:5px 10px;
        float:left
    }
</style>



